body{margin:0;padding:0;width:100vw;height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;overflow:hidden;font-family:"Anuphan",Arial,Helvetica,sans-serif;font-size:18px;color:white;background-image:linear-gradient(120deg, #ff36b5, rgba(255, 255, 255, 0)),linear-gradient(185deg, #369cff, rgba(255, 255, 255, 0)),linear-gradient(340deg, #ffe100, rgba(255, 255, 255, 0))}.screen-reader-only{position:absolute;width:1px;height:1px;padding:0;margin:-1px;overflow:hidden;clip:rect(0, 0, 0, 0);white-space:nowrap;border:0}#logo_h1{display:inline-block;width:100%;max-width:40em}main{text-align:center;display:flex;flex-direction:column;gap:1em;max-width:100%;margin:0 1em}main nav{font-size:1.5rem;display:flex;flex-direction:column;gap:1em}@media screen and (min-width: 64rem){main nav{display:grid;grid-template-columns:repeat(3, 1fr)}}main nav div{border-radius:.25em}main nav div a{display:flex;align-items:center;gap:.5em;padding:.5em 2em;color:inherit;text-decoration:unset}main nav div a svg{height:1em;min-width:1em}main nav div#nav_booth{background-color:#ff5c67}main nav div#nav_booth:hover{background-color:#ff646e}main nav div#nav_booth:active{background-color:#ff6b75}main nav div#nav_github{background-color:#24292f}main nav div#nav_github:hover{background-color:#2f363d}main nav div#nav_github:active{background-color:#3a424c}main nav div#nav_vpm{background-color:#35b6d6}main nav div#nav_vpm:hover{background-color:#46bcd9}main nav div#nav_vpm:active{background-color:#57c2dd}footer{position:absolute;bottom:0;padding:1em}